<template>
  <div v-if="loading" class="loading-spinner container-fluid">
    <div class="d-flex justify-content-center pt-5 mt-5">
      <div class="spinner-border text-primary" role="status">
        <span class="visually-hidden">Loading...</span>
      </div>
      <p>Loading</p>
    </div>
  </div>
  <div v-if="!title && !loading">
    <NotFound/>
  </div>
  <div class="home bg-dark" v-else>
    <AdDetect v-show="selectedServer != '' && !loading" />
    <div class="container-xxl pt-3 pb-3">
        <div v-if="selectedServer === '' && !loading" class="container text-center text-light">
          <h2>Coming Soon</h2>
        </div>
        <div v-else class="ratio ratio-21x9">
            <iframe class="rounded-4" title="Video Player" id="iframe-embed" width="100%" height="500" scrolling="no" data-clarity-unmask="true" frameborder="0" :src="selectedServer" allowfullscreen webkitallowfullscreen="true" mozallowfullscreen="true" referrerpolicy="origin"></iframe>
        </div>
        <div class="text-center" v-show="servers != null">
          <p class="text-light">Servers</p>
          <div class="btn-group" role="group" aria-label="Server Select">
            <button type="button" class="btn btn-outline-primary" v-for="server, index in servers" :key="index" @click="setSelectServer(server.url)">Server {{ server.name }} {{ index + 1 }}</button>
          </div>
        </div>
    </div>
    <div class="aboutMovie" :style="backgroundImage">
        <div class="container-fluid bg-dark pt-5 pb-5" style="--bs-bg-opacity: .7;">
            <div class="container-xxl row">
                <div class="col m-2 text-center">
                    <img :src="movieImage" class="moviePoster" :alt=title>
                </div>
                <div class="col text-light">
                    <p><font-awesome-icon icon="fa-solid fa-star-half-stroke" /> {{ rating }}/10 - <font-awesome-icon icon="fa-solid fa-calendar-day" /> Released {{ release_date }} - <font-awesome-icon icon="fa-solid fa-clock" /> {{ runtime }} min</p>
                    <h1>{{ title }}</h1>
                    <p class="lead">{{ tagline }}</p>
                    <p>{{ description }}</p>
                    <p>{{ metaDescription }}</p>
                    <p>Genre:</p>
                    <ul class="list-group list-group-horizontal">
                        <li class="list-group-item list-group-item-dark" v-for="item in genre" :key=item>
                          <router-link :to="'/movies/' + item.id + '/1'">{{ item.name }}</router-link>
                        </li>
                    </ul>
                    <ul class="list-unstyled list-inline">
                        <li class="list-inline-item" v-for="item in keywords" :key=item>
                          {{ item.name }}
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <SimilarMovies v-if="!loading" :id="movie" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { getMovieDetails } from '@/services/IMDBService'
import NotFound from '@/views/errors/404.vue'
import AdDetect from '@/components/AdDetect.vue'
import SimilarMovies from '@/components/SimilarMovies.vue'
import { getMovieServers } from '@/services/db'
import { event, time } from 'vue-gtag'
import { useHead } from '@unhead/vue'
import { firstAd } from '@/services/mone'

export default defineComponent({
  name: 'WatchMovieView',
  components: {
    NotFound,
    SimilarMovies,
    AdDetect
  },
  data () {
    return {
      movie: this.$route.params.movie,
      title: '',
      imdb: '',
      description: '',
      movieImage: '',
      rating: 0,
      genre: {},
      release_date: 0,
      runtime: '',
      backdropImage: '',
      tagline: '',
      spoken_languages: {},
      servers: null as Array<any> | null,
      selectedServer: '',
      loading: true,
      metaDescription: '',
      keywords: [],
      domain: location.hostname
    }
  },
  created () {
    this.getMovieDetails()
  },
  mounted () {
    this.timeTrack()
    // wait 30 seconds before adding the sponder
    setTimeout(() => {
      this.addSponder()
    }, 40000)
  },
  methods: {
    addSponder () {
      // create a script tag and add it to the header
      const script = document.createElement('script')
      // set 'data-cfasync'
      script.setAttribute('data-cfasync', 'false')
      // set content of script to firstAd
      script.innerHTML = firstAd
      // give the script an id
      script.setAttribute('my-att', 'sponder')
      // append the script tag to the header
      document.head.appendChild(script)
    },
    removeSponder () {
      // remove the script tag from the header
      const script = document.querySelector('script[my-att="sponder"]')
      // check if the script tag exists
      if (script !== null) {
        // remove the script tag
        script.remove()
      }
    },
    setSelectServer (server: string) {
      if (server.includes('sbrapid.com') || server.includes('hlswish.com.com') || server.includes('playerwish.com') || server.includes('wishonly.site') || server.includes('strwish.com') || server.includes('swdyu.com') || server.includes('swhoi.com') || server.includes('jodwish.com') || server.includes('sblona.com') || server.includes('sbsonic.com') || server.includes('sblanh.com') || server.includes('lvturbo.com') || server.includes('sbhight.com') || server.includes('sbbrisk.com') || server.includes('sbrity.com') || server.includes('sbchill.com') || server.includes('sbspeed.com') || server.includes('sblongvu.com') || server.includes('sbanh.com') || server.includes('streamsb') || server.includes('vidmovie.xyz')) {
        // eslint-disable-next-line
        // @ts-ignore
        this.selectedServer = server + '?poster=' + this.backdropImage
      } else if (server.includes('streamtape.com')) {
        this.selectedServer = server + '?thumb=' + this.backdropImage
      } else if (server.includes('dood.so') || server.includes('d0000d.com') || server.includes('d000d.com') || server.includes('d00d.com')) {
        this.selectedServer = server + '?c_poster=' + this.backdropImage
      } else {
        this.selectedServer = server
      }
    },
    getMovieDetails () {
      getMovieDetails(this.movie).then(response => {
        this.description = response.overview
        this.title = response.title
        this.movieImage = 'https://image.tmdb.org/t/p/w500' + response.poster_path
        this.backdropImage = 'https://image.tmdb.org/t/p/w1280' + response.backdrop_path
        this.keywords = response.keywords.keywords
        this.rating = response.vote_average
        this.imdb = response.imdb
        this.genre = response.genres
        this.release_date = response.release_date
        this.runtime = response.runtime
        this.tagline = response.tagline
        this.spoken_languages = response.spoken_languages
        document.title = 'Watch ' + response.title + ' Free Online - MovieLair'
        this.metaDescription = 'Watch ' + response.title + ' free online with no ads through streaming or download!'
        useHead({
          title: 'Watch ' + this.title + ' Free Online' || 'Watch Movies Free Online',
          meta: [
            {
              name: 'description',
              content: this.metaDescription
            },
            { property: 'og:title', content: this.metaDescription },
            { property: 'twitter:title', content: this.metaDescription },
            { property: 'og:image', content: this.movieImage },
            { property: 'twitter:image', content: this.movieImage }
          ]
        })
        event('watch_movie', {
          event_category: 'engagement',
          event_label: 'Watch Movie',
          value: response.title
        })
        if (response.title) {
          getMovieServers(Number(this.movie)).then(result => {
            if (result.data != null) {
              this.servers = result.data.servers.slice(0, 5)
              // If there are less than 3 servers keep adding default servers until the total is 3
              if (this.servers && this.servers.length < 2) {
                this.servers.push({ url: 'https://s1.movielair.lol/embed/movie/' + this.movie })
                this.servers.push({ url: 'https://embed.smashystream.com/playere.php?tmdb=' + this.movie })
              } else if (this.servers && this.servers.length < 3) {
                this.servers.push({ url: 'https://s1.movielair.lol/embed/movie/' + this.movie })
              }

              if (result.data.servers[0].url.includes('swhoi.com') || result.data.servers[0].url.includes('hlswish.com') || result.data.servers[0].url.includes('playerwish.com') || result.data.servers[0].url.includes('wishonly.site') || result.data.servers[0].url.includes('strwish.com') || result.data.servers[0].url.includes('swdyu.com') || result.data.servers[0].url.includes('jodwish.com') || result.data.servers[0].url.includes('cdnwish.com') || result.data.servers[0].url.includes('asnwish.com')) {
                // eslint-disable-next-line
                // @ts-ignore
                this.selectedServer = result.data.servers[0].url + '?poster=' + this.backdropImage
              } else if (result.data.servers[0].url.includes('streamtape.com')) {
                this.selectedServer = result.data.servers[0].url + '?thumb=' + this.backdropImage
              } else if (result.data.servers[0].url.includes('dood.so') || result.data.servers[0].url.includes('d0000d.com') || result.data.servers[0].url.includes('d000d.com') || result.data.servers[0].url.includes('d00d.com')) {
                this.selectedServer = result.data.servers[0].url + '?c_poster=' + this.backdropImage
              } else {
                this.selectedServer = result.data.servers[0].url
              }
            } else {
              this.servers = [
                { url: 'https://s1.movielair.lol/embed/movie/' + this.movie },
                { url: 'https://embed.smashystream.com/playere.php?tmdb=' + this.movie },
                { url: 'https://player.autoembed.cc/embed/movie/' + this.movie },
                { url: 'https://multiembed.mov/directstream.php?video_id=' + this.movie + '&tmdb=1' }
              ]
              this.selectedServer = this.servers[0].url
            }
            this.loading = false
          })
        }
      })
    },
    timeTrack () {
      // Feature detects Navigation Timing API support.
      if (window.performance) {
        // Gets the number of milliseconds since page load
        // (and rounds the result since the value must be an integer).
        const timeSincePageLoad = Math.round(performance.now())

        // send timing event
        time({
          name: 'movie_page_load',
          value: timeSincePageLoad,
          event_category: 'Overall Application Performance'
        })
      }
    }
  },
  computed: {
    backgroundImage (): object {
      return {
        'background-image': `url(${this.backdropImage})`
      }
    }
  },
  onUnmounted () {
    this.removeSponder()
  }
})
</script>

<style scoped lang="scss">
.aboutMovie {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.moviePoster {
    max-height: 400px;
}

.loading-spinner {
  min-height: 80vh;
}
</style>
