<script lang="ts">
import { defineComponent } from 'vue'
import { getSimilarMovies } from '@/services/IMDBService'
import CatalogPlaceholder from '@/components/CatalogPlaceholder.vue'

export default defineComponent({
  name: 'SimilarMovies',
  props: {
    id: String
  },
  components: {
    CatalogPlaceholder
  },
  data () {
    return {
      trending: [],
      currentDomain: window.location.hostname,
      loading: true
    }
  },
  created () {
    this.getSimilarMovies()
  },
  methods: {
    truncate (str, n) {
      return (str.length > n) ? str.slice(0, n - 1) + '...' : str
    },
    getSimilarMovies () {
      getSimilarMovies(this.id).then(response => {
        this.trending = response.results
        this.loading = false
      })
    }
  }
})
</script>

